header {
	background-color: rgba(51, 51, 51, 0.4);
	position: fixed;
	width: 100%;
	z-index: 2;
}
.block {
	display: none;
	position: absolute;
	background-color: black;
	width: 100%;
	height: 1500px;
	text-align: center;
	z-index: 1;
}
.block_div {
	display: flex;
	max-width: 980px;
	margin: 0 auto;
	justify-content: space-between;
	text-align: left;
}
.block h1, h3 {
	display: flex;
}
.block p {
	max-width: 500px;
}
.block_back_p {
	padding-top: 150px;
}
.block p span {
	color: aqua;
	font-size: 15px;
}
.block p a {
	color: white;
	font-size: 15px;
}
.block_img {
	margin-left: calc(100px);
}
.block h1 {
	font-size: 40px;
	margin-top: 100px;
}
.block p {
	font-size: 17px;
	margin-top: 20px;
}
.block h3 {
	font-size: 30px;
	margin-top: 50px;
}
.block_div div:last-child p {
	font-size: 35px;
}
.block_price_with_sale {
	display: block;
	text-decoration: line-through;
	padding-top: 100px;
}
.block_div div:last-child button {
	width: 200px;
	height: 70px;
}
@media(max-width: 430px) {
	.block_div {
		flex-direction: column;
		align-items: center;
	}
	.block_img {
		width: 100%;
		margin-left: 0;
	}
	.block_one {
		padding-left: 12px;
		padding-right: 12px;
	}
	.block_div div:last-child p {
		font-size: 20px;
	}
	.block_price_with_sale {
		display: block;
		width: 50px;
		height: 20px;
		padding-top: 0;
	}
	.blocK_price {
		display: flex;
		width: 50px;
		height: 20px;
	}
	.block_two_gg {
		height: 100px;
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
	
}
.typed-cursor {
	display: none;
}
header .container {
	max-width: 1168px;
}
.header_elements {
	display: flex;
	justify-content: space-between;
	align-content: center;
	padding-top: 40px;
	padding-bottom: 40px;


}
.shopping_cart {
	display: flex;
	align-items: center;

}
.shopping_cart span {
	padding-left: 10px;
}
.shopping_cart img {
	display: inline;
}
.li_market div {
	display: none;
	padding-top: 10px;
	position: absolute;
	text-align: center;
	padding-top: 45px;
	width: 120px;
	height: 100px;
}
.li_market div a {
	background-color: rgba(51, 51, 51, 0.4);
	display: inline-block;
	padding-bottom: 20px;
	width: 110px;
}
.logo {
	margin-left: 30px;
	font-size: 20px;
}
.contacts_elems {
	display: flex;
}
.contacts_elems li {
	margin-right: 30px;
	font-size: 15px;
}
@media(max-width: 425.9px) {
	.header_elements li {
		display: none;
	}
}
.hero {
	display: block;
	width: 100%;
	height: 750px;
	background: url('../photos/forest.jpg') 0 0/100% 100% no-repeat;
	text-align: center;
}
.hero h1 {
	padding-top: 300px;
	font-size: 45px;
	font-weight: 700;

}
.hero img {
	margin-top: 200px;
	width: 60px;
	height: 60px;
}
@media(max-width: 425.9px) {
	.hero {
		height: 600px;
	}
	.hero h1 {
		padding-top: 200px;
		font-size: 35px;
	}
	
}
.new_in_market {
	display: flex;

}
.new_in_market div {
	display: flex;
	width: 50%;
	height: 600px;
}
.new_in_market .block_one h2 {
	max-width: 250px;
	font-size: 30px;
	font-weight: 700;
   align-self: center;
   letter-spacing: 2px;
   line-height: 1.4;
}
.new_block_two {
	background: url('../photos/new.jpg') 0 0/100% 100% no-repeat;
}
@media(max-width: 768.9px) {
	.new_in_market div {
		height: 400px;
	}
}
@media(max-width: 425.9px) {
	.new_in_market {
		display: block;
		text-align: center;
		width: 100%;
	}
	.new_in_market div {
		width: 100%;
		height: 300px;
	}
	.new_in_market .block_one {
		width: 100%;
	}
}
.goods ul {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.goods ul li {
	height: 400px;
	width: 33.3%;
	background-color: blue;
}
.goods_one {
	background: url('../photos/goods/one.jpg') 0 0/100% 100% no-repeat;
}
.goods_two {
	background: url('../photos/goods/two.jpg') 0 0/100% 100% no-repeat;
}
.goods_three {
	background: url('../photos/goods/three.jpg') 0 0/100% 100% no-repeat;
}
.goods_hover {
	display: none;
	position: relative;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	justify-content: center;
}
.goods_hover h3 {
	align-self: center;
	font-size: 20px;
	font-weight: 700;
}
.goods_one:hover .goods_hover {
	display: inline-flex;
	animation-direction: 1;
}
.goods_two:hover .goods_hover {
	display: inline-flex;
	animation-direction: 1;
}
.goods_three:hover .goods_hover {
	display: inline-flex;
	animation-direction: 1;
}
@media(max-width: 1024px) {
	.goods ul li {
		height: 400px;
	}
}
@media(max-width: 768px) {
	.goods ul li {
		width: 100%;
	}
}
.sale {
	text-align: center;
}
.sale h2 {
	margin-top: 100px;
	font-size: 60px;
	letter-spacing: 2px;
}
.sale_p {
	margin-top: 20px;
	font-size: 30px;
	letter-spacing: 2px;
}
.sale_list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.sale_list li:hover {
	background-color: rgba(128, 128, 128, 0.6);
}
.sale_list li h3 {
	padding-top: 30px;
	font-size: 20px;
}
.sale_price {
	font-size: 15px;
	margin-top: 10px;
	padding-bottom: 30px;
}
.sale_price span {
	text-decoration: line-through;
}
@media(max-width: 1024px) {
	.sale_list {
		justify-content: center;
	}
}
@media(max-width: 425.9px) {
	.sale_list li {
		width: 100%;
	}
	.sale_list li img {
		width: 100%;
	}
}
.about_us {
	width: 100%;
	height: 600px;
	background: url('../photos/about_us_two.jpg') 0 0/100% 100% no-repeat;
}
.about_us .div_one {
	display: flex;
	width: 50%;
	height: 600px;
	background-color: rgba(0, 0, 0, 0.5);
	justify-content: center;
}
.div_two {
	align-self: center;
}
.div_two h3 {
	font-size: 25px;
	margin-bottom: 10px;
}
.about_us div p {
	font-size: 17px;
	max-width: 400px;
}
@media(max-width: 768px) {
	.about_us .div_one {
		width: 100%;
	}
}
@media(max-width: 425.9px) {
	.about_us {
		height: 400px;
	}
}
.contacts .container {
	margin-top: 150px;
	display: flex;
	flex-wrap: wrap;
}
.contacts {
	height: 400px;
}
.contacts_container {
	display: flex;
}
.form{
	display: block;
	max-width: 500px;
	text-align: center;
	
}
.form form input {
	display: inline;
	width: calc(100% - 20px);
	height: 50px;
	margin-bottom: 20px;
	border: 2px solid grey;
	font-size: 17px;
	letter-spacing: 1px;
	font-weight: 700;
}
.textInput {
	height: 100px;
}
.form form button {
	display: block;
	width: 100px;
	height: 30px;
	background-color: black;
	color: white;
	font-size: 20px;
	letter-spacing: 1px;
	font-weight: 700;
	margin-left: calc(100% - 100px);
}
.contacts_texts {
	width: 50%;
	text-align: center;
}
.contacts_tex {
	display: inline-block;
	text-align: start;
}
.contacts_or {
	font-size: 17px;
	font-weight: 700;
	letter-spacing: 1px;
	margin-bottom: 20px;
}
.contacts_text {
	display: inline-block;
	max-width: 400px;	
	font-size: 17px;
	font-weight: 400;
	letter-spacing: 1px;
	margin-bottom: 20px;
}
.contacts_texts h3 {
	font-size: 25px;
	font-weight: 700;
	letter-spacing: 1px;
	margin-bottom: 20px;
}
@media(max-width: 768px) {
    .contacts_container {
		justify-content: center;
	}	
	.contacts {
		height: 800px;
	}
	.contacts_texts {
		position: absolute;
	}
	.form {
		padding-top: 300px;
	}
}
.follow	{
	text-align: center;
	background-color: #222222;
	height: 400px;
}
.follow h3 {
	font-size: 40px;
	font-weight: 700;
	letter-spacing: 1px;
	padding-top: 100px;
	max-width: 630px;
	margin: 0 auto;
}
.follow p {
	font-size: 25px;
	font-weight: 400;
	letter-spacing: 1px;
	margin-top: 20px;	
}
.follow_container {
	display: inline-block;
	width: 600px;
	height: 150px;
	box-shadow: 2px 1px 2px 2px #1A1A1A;
	margin-top: 20px;
	
}
.follow_container form input {
	width: calc(100% - 20px);
	display: inline-block;
	height: 40px;
	font-size: 17px;
	letter-spacing: 1px;
	font-weight: 700;
	margin-top: 20px;
	border: 2px solid white;
	background-color: #222222;
	color: white;
}
.follow_container form button {
	width: calc(100% - 20px);
	height: 40px;
	margin-top: 10px;
	color: white;
	border: 2px solid white;
	background-color: #222222;
	font-size: 17px;
	letter-spacing: 1px;
	font-weight: 700;
}
@media(max-width: 425.9px) {
	.follow_container {
		width: calc(100% - 20px);
	}
	.follow {
		height: 500px;
	}
}
.sale_list_item {
	width: 300px;
	text-align: center;
}
.sale_list_item img {
	width: 300px;
	height: 300px;
}
.sale_list li h3 {
	width: 141px;
	margin: 0 auto;
	text-align: center;
}
.sale_list li p {
	max-width: 50px;
	margin: 0 auto;
}